<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        
        <el-form-item class="right">
          <el-button type="primary" size="mini" icon="el-icon-search" v-on:click="getcopyList"></el-button>
        </el-form-item>
        <el-form-item class="right">
          <el-input v-model="filters.name" size="mini" placeholder="Duotel ID//"></el-input>
        </el-form-item>
        <el-form-item class="left">
          <el-button type="primary" icon="el-icon-plus" size="mini" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="users" border highlight-current-row v-loading="listLoading" style="width: 100%;">
      <el-table-column prop="id" label="ID" min-width="80" align="center" sortable></el-table-column>
      <el-table-column prop="title" label="" min-width="160" align="center" sortable></el-table-column>
      <!-- <el-table-column label="" min-width="160" sortable>
        <template slot-scope="scope">
          <div class="contentIns" v-html="scope.row.content"></div>
        </template>
      </el-table-column> -->
      <el-table-column prop="code" label="code" min-width="80" align="center" sortable></el-table-column>
      <el-table-column prop="create_time" label="" min-width="80" align="center" sortable></el-table-column>
      <el-table-column label="" min-width="100" align="center">
        <template slot-scope="scope">
          <el-button
            type="success"
            @click="handleEdit(scope.$index, scope.row)"
            size="mini"
            plain
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :page-sizes="[10, 20, 30, 50]"
        :page-size="pageSize"
        layout="sizes, prev, pager, next"
        :total="total"
      ></el-pagination>
    </el-col>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
      <el-form
        :model="editForm"
        :hide-required-asterisk="true"
        :rules="editFormRules"
        ref="editForm"
        label-width="80px"
      >
        <el-form-item label="" prop="title">
          <el-input placeholder="" v-model="editForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="code" prop="code">
          <el-input placeholder="code" v-model="editForm.code" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- -->
          <el-upload
            class="avatar-uploader4"
            :action="upUrl"
            name="img"
            :headers="header2"
            :show-file-list="false"
            :on-success="uploadSuccess2"
            :on-error="uploadError2"
            :before-upload="beforeUpload2"
          ></el-upload>

          <quill-editor
            class="editor"
            v-model="editForm.content"
            ref="myQuillEditor"
            :options="editorOption2"
            @blur="onEditorBlur2($event)"
            @focus="onEditorFocus2($event)"
            @change="onEditorChange2($event)"
          ></quill-editor>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click.native="editFormVisible = false"></el-button>
        <el-button size="small" type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form
        :model="addForm"
        :hide-required-asterisk="true"
        :rules="addFormRules"
        ref="addForm"
        label-width="80px"
      >
        <el-form-item label="" prop="title">
          <el-input placeholder="" v-model="addForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="code" prop="code">
          <el-input placeholder="code" v-model="addForm.code" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- -->
          <el-upload
            class="avatar-uploader3"
            :action="upUrl"
            name="img"
            :headers="header"
            :show-file-list="false"
            :on-success="uploadSuccess"
            :on-error="uploadError"
            :before-upload="beforeUpload"
          ></el-upload>

          <quill-editor
            class="editor"
            v-model="addForm.content"
            ref="myQuillEditor"
            :options="editorOption"
            @blur="onEditorBlur($event)"
            @focus="onEditorFocus($event)"
            @change="onEditorChange($event)"
          ></quill-editor>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
            <el-button size="small" @click.native="addFormVisible = false"></el-button>
        <el-button size="small" type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
        
      </div>
    </el-dialog>
  </section>
</template>
<style>
.ql-container.ql-snow {
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  height: auto;
      min-height:200px;
      max-height:350px;
  margin: 0px;
  position: relative;
}
.ql-editor{
      height: auto;
      min-height:200px;
      max-height:350px;
}
</style>
<script>
import { resName } from "../../api/api";
import { quillEditor, Quill } from "vue-quill-editor";
import { container, ImageExtend } from "quill-image-extend-module";
if (!Quill.imports['modules/ImageExtend']) {
      // Register only if not exists - 
      Quill.register('modules/ImageExtend', ImageExtend);
}
// Quill.register("modules/ImageExtend", ImageExtend);
export default {
  components: {
    quillEditor
  },
  data() {
    return {
          upUrl : "",
      filters: {
        name: "",
        options: [],
        value: ""
      },
      users: [],
      total: 0,
      page: 1,
      listLoading: false,

      editFormVisible: false, //
      editLoading: false,
      editFormRules: {
        title: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        code: [
          {
            required: true,
            message: "code",
            trigger: "change"
          }
        ]
      },
      //
      editForm: {
        title: "",
        code: "",
        content: ""
      },

      addFormVisible: false, //
      addLoading: false,
      addFormRules: {
        title: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        code: [
          {
            required: true,
            message: "code",
            trigger: "change"
          }
        ]
      },
      //
      addForm: {
        title: "",
        code: "",
        content: ""
      },
      pageSize: 10,
      quillUpdateImg: false, // loadingfalse,
      editorOption: {
        placeholder: "",
        theme: "snow", // or 'bubble'
        placeholder: "",
        modules: {
          ImageExtend: {
            loading: true, //  
            name: "img", // 
            size: 10, //  M1M = 1024kb
            action: resName.tarUrl(), // , actionbase64
            // response 
            // {code: 200; data:{ url: 'baidu.com'}}
            //  return res.data.url
            response: res => {
              if (res.code === 1) {
                this.$message({
                  message: "",
                  type: "success"
                });
                return res.data;
              }
            }
          },
          toolbar: {
            container: container,
            // container: "#toolbar",
            handlers: {
              image: function(value) {
                if (value) {
                  // input
                  document.querySelector(".avatar-uploader3 input").click();
                } else {
                  this.quill.format("image", false);
                }
              }
            }
          }
        }
      },
      editorOption2: {
        placeholder: "",
        theme: "snow", // or 'bubble'
        placeholder: "",
        modules: {
          ImageExtend: {
            loading: true, //  
            name: "img", // 
            size: 10, //  M1M = 1024kb
            action: resName.tarUrl(), // , actionbase64
            // response 
            // {code: 200; data:{ url: 'baidu.com'}}
            //  return res.data.url
            response: res => {
              if (res.code === 1) {
                this.$message({
                  message: "",
                  type: "success"
                });
                return res.data;
              }
            }
          },
          toolbar: {
            container: container,
            // container: "#toolbar",
            handlers: {
              image: function(value) {
                if (value) {
                  // input
                  document.querySelector(".avatar-uploader4 input").click();
                } else {
                  this.quill.format("image", false);
                }
              }
            }
          }
        }
      },
      header: {
        // token: sessionStorage.token
      }, // token
      header2: {
        // token: sessionStorage.token
      } // token
    };
  },
  created() {
        this.upUrl = resName.tarUrl();
    this.getcopyList();
  },
  mounted() {},
  methods: {
    handleSizeChange(val) {
      //
      this.pageSize = val;
      this.getcopyList();
    },
    handleCurrentChange(val) {
      this.page = val;
      this.getcopyList();
    },
    //
    getcopyList() {
      let para = {
        page: this.page,
        per_page: this.pageSize
      };

      this.listLoading = true;
      resName
        .copyList(para)
        .then(res => {
          //
          //console.log("" + JSON.stringify(res));
          if (res.code === 1) {
            this.total = res.data.total;
            this.users = res.data.data;
            this.listLoading = false;
          } else if (res.code === -7) {
            this.$message({
              message: res.msg,
              type: "error"
            });
            this.listLoading = false;
          }
        })
        .catch(err => {
          //
          // console.log(""+JSON.stringify(err));
          this.listLoading = false;
        });
    },
    //
    handleEdit: function(index, row) {
      this.editFormVisible = true;
      this.editForm = {
        id: row.id,
        title: row.title,
        code: row.code,
        content: row.content
      };
    },
    //
    handleAdd: function() {
      this.addFormVisible = true;
      this.addForm = {
        title: "",
        code: "",
        content: ""
      };
    },
    //
    editSubmit: function() {
      this.$refs.editForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.editLoading = true;
            let para = this.editForm;
            resName
              .copyEdit(para)
              .then(res => {
                //
                //console.log("" + JSON.stringify(res));
                if (res.data.code === 1) {
                  this.$message({
                    message: "",
                    type: "success"
                  });
                  this.editLoading = false;
                  this.editFormVisible = false;
                  this.$refs["editForm"].resetFields();
                  this.getcopyList();
                } else if (res.data.code === -7) {
                  this.$message({
                    message: res.data.msg,
                    type: "error"
                  });
                }
              })
              .catch(err => {
                //
                // console.log(""+JSON.stringify(err));
              });
          });
        }
      });
    },
    //
    addSubmit: function() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          this.$confirm("", "", {}).then(() => {
            this.addLoading = true;
            let para = this.addForm;
            // console.log(JSON.stringify(para));
            resName
              .createCopy(para)
              .then(res => {
                //
            //     console.log("" + JSON.stringify(res));
                if (res.data.code === 1) {
                  this.$message({
                    message: "",
                    type: "success"
                  });
                  this.addLoading = false;
                  this.addFormVisible = false;
                  this.$refs["addForm"].resetFields();
                  this.getcopyList();
                } else if (res.code === -7) {
                  this.$message({
                    message: res.msg,
                    type: "error"
                  });
                }
              })
              .catch(err => {
                //
                // console.log(""+JSON.stringify(err));
              });
          });
        }
      });
    },
    onEditorBlur() {
      //
    },
    onEditorFocus() {
      //
    },
    onEditorChange() {
      //
      this.$emit("input", this.content);
    },
    onEditorBlur2() {
      //
    },
    onEditorFocus2() {
      //
    },
    onEditorChange2() {
      //
      this.$emit("input", this.content);
    },
    // 
    beforeUpload() {
      // loading
      this.quillUpdateImg = true;
    },

    uploadSuccess(res, file) {
      // console.log(JSON.stringify(res));
      // res
      // 
      let quill = this.$refs.myQuillEditor.quill;
      // 
      if (res.code == 1) {
        // 
        let length = quill.getSelection().index;
        //   res.url
        quill.insertEmbed(length, "image", res.data);
        // 
        quill.setSelection(length + 1);
      } else {
        this.$message.error("");
      }
      // loading
      this.quillUpdateImg = false;
    },
    // 
    uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    },
    beforeUpload2() {
      // loading
      this.quillUpdateImg = true;
    },

    uploadSuccess2(res, file) {
      // console.log(JSON.stringify(res));
      // res
      // 
      let quill = this.$refs.myQuillEditor.quill;
      // 
      if (res.code == 1) {
        // 
        let length = quill.getSelection().index;
        //   res.url
        quill.insertEmbed(length, "image", res.data);
        // 
        quill.setSelection(length + 1);
      } else {
        this.$message.error("");
      }
      // loading
      this.quillUpdateImg = false;
    },
    // 
    uploadError2() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    }
  }
};
</script>

<style>
.el-dialog__header {
  border-bottom: 1px solid #ddd;
  background: #ddd;
}
.el-dialog__footer {
  border-top: 1px solid #ddd;
}
.toolbar,
.el-table {
  background: #fff;
}
.toolbar {
  margin-bottom: 15px;
}
.langItem .el-form-item {
  width: 40%;
  margin-right: 1%;
  display: inline-block;
}
.langItem .el-button {
  width: 16%;
  display: inline-block;
  float: right;
}
/* .quill-editor {
  height: 200px;
}

.ql-container {
  height: 150px;
} */

.ql-snow .ql-picker {
  line-height: initial;
}
.contentIns {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
}
/**/
.editor {
  line-height: normal !important;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: ":";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}
.avatar-uploader3 {
  display: none;
}
.el-form-item.right{
      float: right;
}
.el-form-item.left{
      float: left;
}
</style>


