<template>
  <section>
    <!--  -->
    <!---->
    <!--  -->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filter">
        <el-form-item class="right">
          <el-button type="primary" size="mini" icon="el-icon-search" v-on:click="getarticleList"></el-button>
        </el-form-item>
        <el-form-item class="right">
          <el-input v-model="filter.ID" size="mini" placeholder="ID / "></el-input>
        </el-form-item>
        <el-form-item class="left">
          <el-button type="primary" size="mini" icon="el-icon-plus" @click="createList"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--  -->
    <el-row type="flex" class="row-bg" justify="space-between">
      <el-col :span="24">
        <div class="grid-content bg-purple">
          <el-table
            ref="multipleTable"
            :data="tableData"
            border
            tooltip-effect="dark"
            style="width: 99.9%"
            stripe
            text-align="center"
            v-loading="listLoading"
            @selection-change="handleSelectionChange"
          >
            <el-table-column prop="id" label="ID" align="center" width="120">
              <template slot-scope="scope">{{ scope.row.id }}</template>
            </el-table-column>
            <el-table-column
              prop="title"
              label=""
              align="center"
              width="320"
              show-overflow-tooltip
            >
              <template slot-scope="scope">{{ scope.row.title }}</template>
            </el-table-column>
            <el-table-column
              prop="listorder"
              label=""
              sortable
              align="center"
              width="120"
              show-overflow-tooltip
            >
              <template slot-scope="scope">{{ scope.row.listorder }}</template>
            </el-table-column>
            <el-table-column prop="view" label="" sortable align="center" width="120">
              <template slot-scope="scope">{{ scope.row.view }}</template>
            </el-table-column>
            <el-table-column prop="release_time" label="" sortable align="center">
              <template slot-scope="scope">{{ scope.row.release_time }}</template>
            </el-table-column>
            <el-table-column prop="is_top" label="" sortable align="center">
              <template slot-scope="scope">{{ scope.row.is_top | filter2 }}</template>
            </el-table-column>
            <el-table-column label="" align="center" width="250">
              <template slot-scope="scope">
                <el-button
                  type="success"
                  v-if="scope.row.status == 0"
                  plain
                  size="mini"
                  @click="enable(scope.row.id)"
                ></el-button>
                <el-button
                  type="danger"
                  v-else-if="scope.row.status == 1"
                  plain
                  size="mini"
                  @click="enable(scope.row.id)"
                ></el-button>
                <el-button
                  type="success"
                  plain
                  size="mini"
                  @click.native="handleLook(scope.$index, scope.row)"
                ></el-button>
                <el-button
                  size="mini"
                  plain
                  type="danger"
                  @click="handleDelete(scope.$index, scope.row)"
                ></el-button>
              </template>
            </el-table-column>
          </el-table>
        </div>
      </el-col>
    </el-row>
  
  </section>
</template>
<style>
.ql-container.ql-snow {
box-sizing: border-box;
font-family: Helvetica, Arial, sans-serif;
font-size: 13px;
height: auto;
min-height:200px;
max-height:350px;
margin: 0px;
position: relative;
}
.ql-editor{
height: auto;
min-height:200px;
max-height:350px;
}
</style>
<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import axios from "axios";
import { quillEditor, Quill } from "vue-quill-editor";
import { container, ImageExtend } from "quill-image-extend-module";
if (!Quill.imports['modules/ImageExtend']) {
      // Register only if not exists - 
      Quill.register('modules/ImageExtend', ImageExtend);
}
// Quill.register("modules/ImageExtend", ImageExtend);
import { resName } from "../../api/api";
export default {
  components: {
    quillEditor
  },
  data() {
    return {
      teUrl: "",
      filter: {
        ID: ""
      },
      file1: {},
      file2: {},
      tableData: [],
      editFormVisible: false, //
      editLoading: false,
      discontinue: false,
      enableing: false,
      discontinues: false,
      enabling: false,
      enables: {
        id: "",
        status: ""
      },
      
      value: "",
      input: "",
      total: 43,
      pageSize: 10,
      page: 1,
      multipleSelection: [],
      listLoading: false,
      quillUpdateImg: false, // loadingfalse,
      
      header: {
        // token: sessionStorage.token
      } // token
    };
  },
  created() {
    this.teUrl = resName.tarUrl();
    
  },
  mounted() {
    this.getarticleList();
  },
  methods: {
    // 
    getarticleList: function() {
      let id = window.location.href.split("=")[1];
      let params = {
        cat_id: id,
        order_field: "release_time",
        order_type: "desc",
        search_key: this.filter.ID
      };
      this.listLoading = true;
      resName
        .articleList(params)
        .then(res => {
          //
          if (res.status === 200) {
            if (res.data.code === 1) {
              this.tableData = res.data.data
              
              this.listLoading = false;
            } else if (res.code === -7) {
              this.$message({
                message: res.msg,
                type: "error"
              });
              this.listLoading = false;
            }
          }
        })
        .catch(err => {
          //
          // console.log(""+JSON.stringify(err));
          this.listLoading = false;
        });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
      handleAvatarSuccess1(res, file) {
      this.editForm.image = URL.createObjectURL(file.raw);
    },
    handleAvatarSuccess2(res, file) {
      this.addForm.image = URL.createObjectURL(file.raw);
    },
    // 
    handleChange1(file) {
      this.file1 = file.raw;
    },
    handleChange2(file) {
      this.file2 = file.raw;
    },
    uploadMainImg1() {
      var _this = this;
      // 
      let form = new FormData();
      //  
      form.append("file", this.file1);
      form.append("action", "UploadVMKImagePath");
      resName
        .uploadImg(form)
        .then(res => {
          if (res.data.code === 1) {
            _this.editForm.image = res.data.data;
          }
        })
        .catch(err => {
          //
          // console.log(""+JSON.stringify(err));
        });
    },
    uploadMainImg2() {
      var _this = this;
      // 
      let form = new FormData();
      //  
      form.append("file", this.file2);
      form.append("action", "UploadVMKImagePath");
      resName
        .uploadImg(form)
        .then(res => {
          if (res.data.code === 1) {
            _this.addForm.image = res.data.data;
          }
        })
        .catch(err => {
          //
          // console.log(""+JSON.stringify(err));
        });
    },
    // 
    enable(id) {
      this.enables.id = id;
      let obj = {};
      obj = this.tableData.find(item => {
        //tablelist
        return item.id === id; //
      });
      this.enables.status = obj.status == 1 ? 0 : 1;
       const h = this.$createElement;
        this.$msgbox({
          title: '',
           type: 'warning',
          message:
          obj.status == 1 ?  h('div', null, [
            h('div', null, ' ')
          ]):h('div', null, [
            h('div', null, ' ')
          ]),
          showCancelButton: true,
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(action => { 
          resName.webarticlerelease(this.enables).then(res => {
              //
              if (res.data.code === 1) {
                this.Abilitazione = false;
                this.getarticleList();
              } else if (res.code === -2) {
                this.$message({
                  message: res.msg,
                  type: "error"
                });
              }
            })
          })
          .catch(err => {
              //
            });
    },
   
   
    
    // 
    createList: function() {
      let id = window.location.href.split("=")[1];
      this.$router.push({ path: "/addArticle", query: {id:id,cad:2}})
    },
    
    // 
    handleDelete(index, row) {
      let params = {
        id: row.id,
        cat_id: row.cat_id
      };
      this.$confirm("", "", {
        type: "warning"
      })
        .then(() => { 
            resName
              .delarticle(params)
              .then(res => {
                //
                // console.log("" + JSON.stringify(res.data));
                if (res.data.code === 1) {
                  this.$message({
                    message: "",
                    type: "success"
                  });
                  this.getarticleList();
                } else if (res.code === -2) {
                  this.$message({
                    message: res.msg,
                    type: "error"
                  });
                }
              })
              .catch(err => {
                //
                // console.log(""+JSON.stringify(err));
              });
         });
    },
    onEditorBlur() {
      //
    },
    onEditorFocus() {
      //
    },
    onEditorChange() {
      //
      this.$emit("input", this.content);
    },
    // 
    beforeUpload() {
      // loading
      this.quillUpdateImg = true;
    },

    uploadSuccess(res, file) {
      // console.log(JSON.stringify(res));
      // res
      // 
      let quill = this.$refs.myQuillEditor.quill;
      // 
      if (res.code == 1) {
        // 
        let length = quill.getSelection().index;
        //  res.url
        quill.insertEmbed(length, "image", res.data);
        // 
        quill.setSelection(length + 1);
      } else {
        this.$message.error("");
      }
      // loading
      this.quillUpdateImg = false;
    },
    // 
    uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    },
    // 
    handleLook(index, row) {
      this.$router.push({ path: "/editArticles", query: { id: row.id} });
    },
  },
  filters: {
    
    // 
    filter2: function(value) {
      if (value == 0) {
        return "";
      } else if (value == 1) {
        return "";
      }
    }
  }
};
</script>

<style scoped lang="less">
body {
  padding-right: 0 !important ;
}
.el-row {
  margin-bottom: 10px;
  border-radius: 4px;
}
.el-col {
  border-radius: 4px;
}
.center {
  text-align: center;
  margin-bottom: 20px;
  margin-top: 20px;
}
.el_co_r {
  color: red;
}
.el-dialog {
  width: 60%;
}
.grid-content {
  border-radius: 4px;
}
.row-bg {
  padding: 12px 10px;
  background: #fff;
}
.el-table td,
.el-table th {
  text-align: center;
}
.imgshow {
  width: 100%;
  height: 100px;
}

.avatar-uploader .el-upload,
.avatar-uploader2 .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader2 {
  display: inline-block;
  position: relative;
  top: 12px;
}
.avatar-uploader .el-upload:hover,
.avatar-uploader2 .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon,
.avatar-uploader-icon2 {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
  border: 1px dashed #d9d9d9;
}
.avatar-uploader-icon2 {
  line-height: 30px;
  text-align: center;
  width: 30px;
  height: 30px;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
  border: 1px dashed #d9d9d9;
}
.avatar2 {
  width: 30px;
  height: 30px;
  display: inline-block;
}
.showImg {
  position: relative;
  top: 10px;
}
.toolbar,
.el-table {
  background: #fff;
}
.toolbar {
  margin-bottom: 15px;
}
.el-form-item.right{
      float: right;
}
.el-form-item.left{
      float: left;
}
/**/

.editor {
  line-height: normal !important;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: ":";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}
.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}
.avatar-uploader3 {
  display: none;
}

</style>



