<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item class="left">
          <el-button type="primary" size="mini" icon="el-icon-plus" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="projectCategory"
      highlight-current-row
      text-align="center"
      v-loading="listLoading"
      style="width: 99.9%;"
      border
    >
      <el-table-column prop="class_value" label="" align="center"></el-table-column>
      
      <el-table-column label="" width="250" align="center">
        <template slot-scope="scope">
          <el-button
            type="success"
            @click="handleEdit(scope.$index, scope.row)"
            size="mini"
            plain
          ></el-button>
          <el-button type="danger" @click="handleDel(scope.$index, scope.row)" size="mini" plain></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      height="30%"
      :visible.sync="editFormVisible"
      :close-on-click-modal="false"
    >
      <el-form
        :model="editForm"
        :hide-required-asterisk="true"
        :rules="editFormRules"
        ref="editForm"
        label-width="80px"
      >
        <el-form-item label="" prop="class_value">
          <el-input placeholder="" v-model="editForm.class_value" class="input-with-select"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click.native="editFormVisible = false"></el-button>
        <el-button size="small" type="primary" @click.native="editSubmit"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      height="30%"
      :visible.sync="addFormVisible"
      :close-on-click-modal="false">
      <el-form
        :model="addForm"
        :hide-required-asterisk="true"
        :rules="addFormRules"
        ref="addForm"
        label-width="80px"
      >
        <el-form-item label="" prop="class_value">
          <el-input placeholder="" v-model="addForm.class_value" class="input-with-select"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click.native="addFormVisible = false"></el-button>
        <el-button size="small" type="primary" @click.native="addSubmit"></el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { resName } from "../../../api/api";
export default {
  data() {
    return {
      listLoading: false,
      projectCategory: [],
      addFormVisible: false,
      editFormVisible: false,
      addForm: {
        class_value: ""
      },
      addFormRules: {
        class_value: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      editForm: {
        class_value: ""
      },
      editFormRules: {
        classValue: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      // classValue:"",
      id: ""
    };
  },
  created() {
    this.commonsdata();
  },
  methods: {
    //   
    commonsdata() {
      let params = {
        class_type: "project_category"
      };
      resName
        .webgetcommonsdata(params)
        .then(res => {
          //
          if (res.code === 1) {
            this.projectCategory = res.data;
          }
        })
        .catch(err => {
          //
          return false;
        });
    },
    // 
    handleAdd() {
      this.addFormVisible = true;
    },
    // 
    addSubmit() {
       this.$refs.addForm.validate(valid => {
           if (valid) {
      let params = {
        class_type: "project_category",
        class_value: this.addForm.class_value
      };
      resName
        .addCommonsData(params)
        .then(res => {
          //
          console.log(res);
          
          if (res.data.code === 1) {
            this.$message({
              message: "",
              type: "success"
            });
            this.addFormVisible = false;
            this.commonsdata();
          } else if (res.data.code === -2) {
            this.$message({
              message: res.msg,
              type: "error"
            });
          }
        })
        .catch(err => {
          //
          return false;
        });
           }
           })
    },
    // 
    handleEdit(index, row) {
      this.editFormVisible = true;
      this.id = row.id;
      this.editForm.class_value = row.class_value
    },
    // 
    editSubmit() {
       this.$refs.editForm.validate(valid => {
           if (valid) {
      let params = {
        id: this.id,
        class_type: "project_category",
        class_value: this.editForm.class_value
      };
      resName
        .editCommonsData(params)
        .then(res => {
          //
          if (res.data.code === 1) {
            this.$message({
              message: "",
              type: "success"
            });
            this.editFormVisible = false;
            this.commonsdata();
          } else if (res.data.code === -2) {
            this.$message({
              message: res.msg,
              type: "error"
            });
          }
        })
        .catch(err => {
          //
          return false;
        });
           }
       })
    },
    // 
    handleDel(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      }).then(() => {
        let params = {
          id: row.id
        };
        resName
          .delCommonsData(params)
          .then(res => {
            //
            if (res.data.code === 1) {
              this.$message({
                message: "",
                type: "success"
              });
              this.commonsdata();
            } else if (res.code === -2) {
              this.$message({
                message: res.msg,
                type: "error"
              });
            }
          })
          .catch(err => {
            //
            return false;
          });
      });
    }
  }
};
</script>
<style lang="less" scoped>
.toolbar,
.el-table {
  background: #fff;
}
.toolbar {
  margin-bottom: 15px;
}
.el-form-item.left {
  float: left;
}
</style>



