<template>
  <div id="container">
    <el-tabs type="border-card">
      <el-tab-pane label="">
        <el-form
          :model="addForm"
          :rules="addFormRules"
          ref="addForm"
          :hide-required-asterisk="false"
          label-width="100px">
          <el-form-item label="" prop="title">
            <el-input placeholder="" v-model="addForm.title" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="listorder">
            <el-input placeholder="" v-model="addForm.listorder" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="description">
            <el-input
              type="textarea"
              placeholder=""
              v-model="addForm.description"
              auto-complete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="tags">
            <el-input
              placeholder=","
              v-model="addForm.tags"
              auto-complete="off"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="url">
            <el-input placeholder="" v-model="addForm.url" auto-complete="off"></el-input>
          </el-form-item>
          <el-form-item label="" prop="status">
            <el-radio-group v-model="addForm.status" size="small">
              <el-radio-button label="0"></el-radio-button>
              <el-radio-button label="1"></el-radio-button>
            </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="release_time">
            <el-date-picker
              v-model="addForm.release_time"
              value-format="yyyy-MM-dd"
              type="date"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item label="" prop="image">
            <el-upload
              class="avatar-uploader"
              action
              :on-change="handleChange2"
              :http-request="uploadMainImg2"
              :on-success="handleAvatarSuccess2"
              :show-file-list="false"
            >
              <img v-if="addForm.image" :src="addForm.image" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="" prop="content">
            <el-upload
              class="avatar-uploader3"
              :action="teUrl"
              name="img"
              :headers="header"
              :show-file-list="false"
              :on-success="uploadSuccess"
              :on-error="uploadError"
              :before-upload="beforeUpload"
            ></el-upload>
            <quill-editor
              class="editor"
              v-model="addForm.content"
              ref="myQuillEditor"
              :options="editorOption"
              @blur="onEditorBlur($event)"
              @focus="onEditorFocus($event)"
              @change="onEditorChange($event)"
            ></quill-editor>
          </el-form-item>
        </el-form>
      </el-tab-pane>
    </el-tabs>
    <div class="btnBox">
      <el-button type="primary" size="small" @click="submitEdit"></el-button>
    </div>
  </div>
</template>
<style>
.ql-container.ql-snow {
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  height: auto;
  min-height: 200px;
  max-height: 350px;
  margin: 0px;
  position: relative;
}
.ql-editor {
  height: auto;
  min-height: 200px;
  max-height: 350px;
}
</style>
<script>
import "quill/dist/quill.core.css";
import "quill/dist/quill.snow.css";
import "quill/dist/quill.bubble.css";
import axios from "axios";
import { quillEditor, Quill } from "vue-quill-editor";
import { container, ImageExtend } from "quill-image-extend-module";
if (!Quill.imports["modules/ImageExtend"]) {
  // Register only if not exists - 
  Quill.register("modules/ImageExtend", ImageExtend);
}
import { resName } from "../../api/api";
export default {
  components: {
    quillEditor
  },
  data() {
    return {
      file2: {},
      teUrl: "",
      addFormRules: {
        title: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        status: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        release_time: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      //
      addForm: {
        id: "",
        cat_id: "",
        title: this.title,
        status: "",
        listorder: "",
        url: "",
        image: "",
        content: "",
        tags: "",
        description: "",
        release_time: ""
      },
      multipleSelection: [],
      listLoading: false,
      quillUpdateImg: false, // loadingfalse,
      editorOption: {
        placeholder: "",
        theme: "snow", // or 'bubble'
        placeholder: "",
        modules: {
          ImageExtend: {
            loading: true, //  
            name: "img", // 
            size: 2, //  M1M = 1024kb
            action: resName.tarUrl, // , actionbase64
            // response 
            // {code: 200; data:{ url: 'baidu.com'}}
            //  return res.data.url
            response: res => {
              if (res.code === 1) {
                this.$message({
                  message: "",
                  type: "success"
                });
                return res.data;
              }
            }
          },
          toolbar: {
            container: container,
            // container: "#toolbar",
            handlers: {
              image: function(value) {
                if (value) {
                  // input
                  document.querySelector(".avatar-uploader3 input").click();
                } else {
                  this.quill.format("image", false);
                }
              }
            }
          }
        }
      },
      header: {
        // token: sessionStorage.token
      }
    };
  },
  created() {
    this.teUrl = resName.tarUrl();
  },
  methods: {
    submitEdit() {
      this.$refs.addForm.validate(valid => {
        if (valid) {
          var params = {
            cat_id: this.$route.query.id,
            title: this.addForm.title,
            status: this.addForm.status,
            listorder: this.addForm.listorder,
            release_time: this.addForm.release_time,
            description: this.addForm.description,
            image: this.addForm.image,
            url: this.addForm.url,
            content: this.addForm.content,
            tags: 1
          };
          resName
            .addarticle(params)
            .then(res => {
              //
              if (res.data.code === 1) {
                this.$message({
                  message: "",
                  type: "success"
                });
                this.$refs["addForm"].resetFields();
                this.$router.replace({ path: "/allArticle?id=" + params.cat_id});
              } else if (res.code === -2) {
                this.$message({
                  message: res.msg,
                  type: "error"
                });
              }
            })
            .catch(err => {
              //
              // console.log(""+JSON.stringify(err));
            });
        }
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleAvatarSuccess2(res, file) {
      this.addForm.image = URL.createObjectURL(file.raw);
    },

    // 
    handleChange2(file) {
      this.file2 = file.raw;
    },
    uploadMainImg2() {
      var _this = this;
      // 
      let form = new FormData();
      //  
      form.append("file", this.file2);
      form.append("action", "UploadVMKImagePath");
      resName
        .uploadImg(form)
        .then(res => {
          if (res.data.code === 1) {
            if(_this.addForm.image !== ""){
              let imgUrl = _this.addForm.image;
              resName.deleOldImg(imgUrl);
              }
            _this.addForm.image = res.data.data;
          }
        })
        .catch(err => {
          //
          // console.log(""+JSON.stringify(err));
        });
    },
    onEditorBlur() {
      //
    },
    onEditorFocus() {
      //
    },
    onEditorChange() {
      //
      this.$emit("input", this.content);
    },
    // 
    beforeUpload() {
      // loading
      this.quillUpdateImg = true;
    },

    uploadSuccess(res, file) {
      // console.log(JSON.stringify(res));
      // res
      // 
      let quill = this.$refs.myQuillEditor.quill;
      // 
      if (res.code == 1) {
        // 
        let length = quill.getSelection().index;
        //  res.url
        quill.insertEmbed(length, "image", res.data);
        // 
        quill.setSelection(length + 1);
      } else {
        this.$message.error("");
      }
      // loading
      this.quillUpdateImg = false;
    },
    // 
    uploadError() {
      // loading
      this.quillUpdateImg = false;
      this.$message.error("");
    }
  }
};
</script>

<style scoped lang="less">
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.quill-editor {
  max-height: 400px;
}

.ql-container {
  min-height: 200px;
  max-height: 350px;
}

.ql-snow .ql-picker {
  line-height: initial;
}

.title {
  font-size: 16px;
  margin-bottom: 0px;
  margin-top: 0;
  border-bottom: 1px solid #e5e5e5;
  padding: 10px 0;
}

.specBox {
  padding: 10px;
  border: 1px solid #ccc;
  margin-top: 15px;
}

.specList li {
  padding: 5px 0;
}

.shoptip {
  width: 100%;
  height: 40px;
  margin-top: 15px;
  border: 1px dashed #f00;
  border-radius: 5px;
  line-height: 40px;
  color: red;
  text-indent: 6px;
  background: #ffffcc;
}

.upload-demo {
  display: inline-block;
}
.specItem .el-input {
  width: 200px;
}
.specItem {
  margin-bottom: 10px;
}
.el-checkbox {
  margin-right: 15px;
}
.am-table {
  width: 100%;
  border-collapse: collapse;
}
.am-table .el-input {
  width: 120px;
}
.am-table > thead > tr > th {
  vertical-align: bottom;
  border-bottom: 1px solid #ddd;
}
.am-table > thead > tr > th {
  vertical-align: middle;
}
.am-table > thead > tr > th,
.am-table > tbody > tr > td {
  padding: 0.7rem;
  line-height: 1.6;
  vertical-align: top;
  border: 1px solid #ddd;
  text-align: center;
}
span.red {
  color: #ff4e00;
}
.file {
  position: relative;
  display: inline-block;
  background: #d0eeff;
  border: 1px solid #99d3f5;
  border-radius: 4px;
  padding: 4px 12px;
  overflow: hidden;
  color: #1e88c7;
  text-decoration: none;
  text-indent: 0;
  line-height: 20px;
  top: 12px;
}
.view-img {
  width: 30px;
}
#fileUpload {
  float: left;
}
#picker {
  float: right;
  margin: 4px;
}
.file input {
  position: absolute;
  font-size: 100px;
  right: 0;
  top: 0;
  opacity: 0;
}
.avatar-uploader .el-upload,
.avatar-uploader2 .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}
.avatar-uploader2 {
  display: inline-block;
  position: relative;
  top: 12px;
}
.avatar-uploader .el-upload:hover,
.avatar-uploader2 .el-upload:hover {
  border-color: #409eff;
}
.avatar-uploader-icon,
.avatar-uploader-icon2 {
  font-size: 28px;
  color: #8c939d;
  width: 178px;
  height: 178px;
  line-height: 178px;
  text-align: center;
}
.avatar-uploader-icon2 {
  line-height: 30px;
  text-align: center;
  width: 30px;
  height: 30px;
}
.avatar {
  width: 178px;
  height: 178px;
  display: block;
}
.avatar2 {
  width: 30px;
  height: 30px;
  display: inline-block;
}
.showImg {
  position: relative;
  top: 10px;
}
.el-tabs--border-card {
  box-shadow: none;
}
#container {
  background: #fff;
  -webkit-box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12),
    0 0 6px 0 rgba(0, 0, 0, 0.04);
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.12), 0 0 6px 0 rgba(0, 0, 0, 0.04);
}
.btnBox {
  padding: 10px 5px;
  border: 1px solid #dcdfe6;
  border-top: none;
}

/**/
.editor {
  line-height: normal !important;
}
.ql-snow .ql-tooltip[data-mode="link"]::before {
  content: ":";
}
.ql-snow .ql-tooltip.ql-editing a.ql-action::after {
  border-right: 0px;
  content: "";
  padding-right: 0px;
}

.ql-snow .ql-tooltip[data-mode="video"]::before {
  content: ":";
}

.ql-snow .ql-picker.ql-size .ql-picker-label::before,
.ql-snow .ql-picker.ql-size .ql-picker-item::before {
  content: "14px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="small"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="small"]::before {
  content: "10px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="large"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="large"]::before {
  content: "18px";
}
.ql-snow .ql-picker.ql-size .ql-picker-label[data-value="huge"]::before,
.ql-snow .ql-picker.ql-size .ql-picker-item[data-value="huge"]::before {
  content: "32px";
}

.ql-snow .ql-picker.ql-header .ql-picker-label::before,
.ql-snow .ql-picker.ql-header .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
  content: "1";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
  content: "2";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
  content: "3";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
  content: "4";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
  content: "5";
}
.ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
.ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
  content: "6";
}

.ql-snow .ql-picker.ql-font .ql-picker-label::before,
.ql-snow .ql-picker.ql-font .ql-picker-item::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="serif"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="serif"]::before {
  content: "";
}
.ql-snow .ql-picker.ql-font .ql-picker-label[data-value="monospace"]::before,
.ql-snow .ql-picker.ql-font .ql-picker-item[data-value="monospace"]::before {
  content: "";
}
</style>



