<template>
	<div id="container" class="goodsIns">
		<el-tabs type="border-card">
			<el-tab-pane label="">
				<el-form ref="form" :model="form" :rules="addFormRules" :hide-required-asterisk="true" label-width="80px">
					<el-form-item label="" prop="goods_name">
						<el-input v-model="form.goods_name" placeholder=""></el-input>
					</el-form-item>
                              <el-form-item label="" prop="goods_model">
						<el-input v-model="form.goods_model" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="CPK">
						<el-input v-model="form.goods_cpk" placeholder="CPK"></el-input>
					</el-form-item>
                              <el-form-item label="" prop="material_code">
						<el-input v-model="form.material_code" placeholder=""></el-input>
					</el-form-item>
                              <el-form-item label="" prop="goods_weight">
						<el-input v-model="form.goods_weight" placeholder=""></el-input>
					</el-form-item>
                              <el-form-item label="" prop="is_top">
						 <el-radio-group v-model="form.is_top" size="small">
                                          <el-radio-button label="" value="0"></el-radio-button>
                                          <el-radio-button label="" value="1"></el-radio-button>
                                    </el-radio-group>
					</el-form-item>
                              <el-form-item label="" prop="is_top">
						 <el-radio-group v-model="form.is_fragile" size="small">
                                          <el-radio-button label="" value="1"></el-radio-button>
                                          <el-radio-button label="" value="2"></el-radio-button>
                                    </el-radio-group>
					</el-form-item>
                              <el-form-item label="" prop="is_top">
						 <el-radio-group v-model="form.battery" size="small">
