                                          <el-radio-button label="" value="2"></el-radio-button>
                                    </el-radio-group>
					</el-form-item>
                             
                              <el-form-item  label="" prop="supplier_id">
                                    <el-select v-model="form.supplier_id"  placeholder="" style="display:block;">
                                          <el-option v-for="item in supplier" :key="item.id" :label="item.supplier_name" :value="item.id"></el-option>
                                    </el-select>
                              </el-form-item>
                              <el-form-item label="" required>
                                    <el-col style="width:30%;margin-right:5%;">
                                          <el-form-item prop="value1" style="width:100%;">
                                                <el-select style="display:block;" v-model="form.value1" @change="selGoods" value-key="id" placeholder="">
                                                      <el-option v-for="item in goodsType" :key="item.id" :label="item.goods_type_name" :value="item"></el-option>
                                                </el-select>
                                          </el-form-item>
                                    </el-col>
                                    <el-col v-if="gtype2"  style="width:30%;margin-right:5%;">
                                          <el-form-item prop="value2" style="width:100%;">
                                                <el-select style="display:block;" v-if="gtype2" v-model="form.value2" @change="selGoods2" value-key="id" placeholder="">
                                                      <el-option v-for="item in goodsType2" :key="item.id" :label="item.goods_type_name" :value="item"></el-option>
                                                </el-select>
                                          </el-form-item>
                                    </el-col>
                                    <el-col v-if="gtype3"  style="width:30%;">
                                          <el-form-item prop="goods_type3" style="width:100%;">
                                                <el-select style="display:block;" v-if="gtype3" v-model="form.goods_type3" @change="selGoods3" value-key="id" placeholder="">
                                                      <el-option v-for="item in goodsType3" :key="item.id" :label="item.goods_type_name" :value="item"></el-option>
                                                </el-select>
                                          </el-form-item>
                                    </el-col>
                              </el-form-item>

					<el-form-item label="" prop="market_price">
						<el-input v-model="form.market_price" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="shop_price">
						<el-input v-model="form.shop_price" placeholder=""></el-input>
					</el-form-item>
                              <el-form-item label="" prop="internal_price">
						<el-input v-model="form.internal_price" placeholder=""></el-input>
					</el-form-item>
					<el-form-item label="" prop="distribution_price">
						<el-input v-model="form.distribution_price" placeholder=""></el-input>
					</el-form-item>
                              <!-- 2019.08.20 am(10:30)AB -->
                              <el-form-item label="A" prop="agent_a">
						<el-input v-model="form.agent_a" placeholder="A"></el-input>
					</el-form-item>
					<el-form-item label="B" prop="agent_b">
						<el-input v-model="form.agent_b" placeholder="B"></el-input>
					</el-form-item>
					<!-- <el-form-item label="" prop="goods_img">
                                    <el-upload
                                          class="avatar-uploader"
                                          action=""
                                          :on-change="handleChange"
                                          :http-request="uploadMainImg"
                                          :show-file-list="false">
                                          <img v-if="form.goods_img" :src="form.goods_img" class="avatar">
                                          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
					</el-form-item> -->
					<el-form-item label="" prop="goods_attr" style="width:100%;">
						<el-input type="textarea" v-model="form.goods_attr" placeholder=""></el-input>
					</el-form-item>
					<!-- <el-form-item label="">
                                    <el-upload
                                    class="avatar-uploader3"
                                    :action="upUrl"
                                    name="img"
                                    :headers="header"
                                    :show-file-list="false"
                                    :on-success="uploadSuccess"
                                    :on-error="uploadError"
                                    :before-upload="beforeUpload">
                                    </el-upload>

                                    <quill-editor 
                                    class="editor"
                                    v-model="form.goods_desc"
                                    ref="myQuillEditor" 
                                    :options="editorOption" 
                                    @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
                                    @change="onEditorChange($event)">
                                    </quill-editor>
                              </el-form-item> -->
				</el-form>
			</el-tab-pane>
                  <el-tab-pane label="">
                        <el-form ref="form" :model="form" :rules="addFormRules" :hide-required-asterisk="true" label-width="80px">
                              <el-form-item label="" style="width:100%;">
						<!-- <quill-editor ref="text" v-model="form.goods_desc" class="myQuillEditor" :options="editorOption" /> -->
                                    <!-- -->
                                    <el-upload
                                    class="avatar-uploader3"
                                    :action="upUrl"
                                    name="img"
                                    :headers="header"
                                    :show-file-list="false"
                                    :on-success="uploadSuccess"
                                    :on-error="uploadError"
                                    :before-upload="beforeUpload">
                                    </el-upload>

                                    <quill-editor 
                                    class="editor"
                                    v-model="form.goods_desc"
                                    ref="myQuillEditor" 
                                    :options="editorOption" 
                                    @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
                                    @change="onEditorChange($event)">
                                    </quill-editor>
                              </el-form-item>
                              
                        </el-form>
                  </el-tab-pane>
                  <el-tab-pane label="">
                        <el-form ref="form" :model="form" :rules="addFormRules" :hide-required-asterisk="true" label-width="80px">
                              <el-form-item label="" prop="goods_img" style="width:100%;">
                                    <el-upload
                                          class="avatar-uploader"
                                          action=""
                                          :on-change="handleChange"
                                          :http-request="uploadMainImg"
                                          :show-file-list="false">
                                          <img v-if="form.goods_img" :src="form.goods_img" class="avatar">
                                          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                                    </el-upload>
					</el-form-item>
                        </el-form>
                  </el-tab-pane>
			<el-tab-pane label="" v-if="specsState">
				<div id="specContent">
					<p class="title"></p>
                              <div class="top">
                                <div class='topOne' v-for="(value,index) in specs" :key="index">
                                    <div class="btn_list" v-if="value.title">
                                        <p class="spectit">{{value.title}}</p>
                                        <div class="specItem" v-for="(v,i) in value._list" :key="i">
                                          <el-checkbox v-model="v.is_checked" ></el-checkbox>
                                            <el-input v-model="v.value" size="small" placeholder=""></el-input>
                                            <a :href="v.img" class="showImg" target="_blank">
                                                <!-- <img :src="v.img" class="view-img" v-if="value.is_img"> -->
                                                <img :src="v.img" class="view-img" v-if="v.is_img">
                                            </a>
                                            <a href="javascript:;" class="file" v-if="value.is_img">
                                                <input type="file" :id="'fileUpload-'+i" @change="uploadImg($event,index,i)" :cat="index" :index="i" class="file-upload">
                                            </a>
                                            
                                                <el-button type="success" size="small" v-if="i == 0" @click="newlyAdded(index)"></el-button>
                                                <el-button type="primary" size="small" v-if="i != 0" @click="newlyDel(index,i)"></el-button>
                                        </div>
                                    </div>
                                </div>
                            </div>

					<div class="shoptip">1.</div>
					<p class="title"></p>
                              <div class="am-form-group">
                                    <div class='table' style='margin-top:10px;'>
                                          <table class="am-table">
                                                <thead id='amsThead'>
                                                <tr v-if="tableHeader!=''">
                                                      <th class='norms'></th>
                                                      <th class="agreement" v-for='item in tableHeader' :key="item">{{item}}</th>
                                                      <th class="common-input"><p><span class="red">*</span></p><el-input v-model="market_value"></el-input></th>
                                                      <th class="common-input"><p><span class="red">*</span></p><el-input v-model="shop_price"></el-input></th>
                                                </tr>
                                                </thead>
                                                <tbody id='amsTbody'>
                                                      
                                                <tr v-for="(row,index) in tableData" :key="index">
                                                      <td class='normsC'><input name="recommend" :value="index" type="radio" v-model="checkRadio"></td>
                                                      <td v-for='(item,key) in row' :key="key">{{item}}</td>
                                                      <!-- <td ><el-input  v-model="row.market_value"></el-input></td>
                                                      <td ><el-input  v-model="row.shop_price"></el-input></td> -->
                                                      <td ><input v-model='row.market_value' class="el-input__inner"></td>
                                                      <td ><input v-model='row.shop_price' class="el-input__inner"></td>
                                                </tr>
                                                </tbody>
                                          </table>
                                    </div>
                              </div>
                              
				</div>
			</el-tab-pane>
		</el-tabs>
            <div class="btnBox">
                  <el-button type="primary" size="small" @click='submitEdit'></el-button>
            </div>
	</div>
</template>
<style>
.ql-container.ql-snow {
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  height: auto;
      min-height:200px;
      max-height:350px;
  margin: 0px;
  position: relative;
}
.ql-editor{
      height: auto;
      min-height:200px;
      max-height:350px;
}
</style>
<script>
	import "quill/dist/quill.core.css";
	import "quill/dist/quill.snow.css";
      import "quill/dist/quill.bubble.css";
      import axios from 'axios';
	import {
		quillEditor,Quill
      } from "vue-quill-editor";
      import {container, ImageExtend} from 'quill-image-extend-module'
      if (!Quill.imports['modules/ImageExtend']) {
            // Register only if not exists - 
            Quill.register('modules/ImageExtend', ImageExtend);
      }
      // Quill.register('modules/ImageExtend', ImageExtend)
	import {
		resName
	} from "../../api/api";
	export default {
		components: {
			quillEditor
		},
		data() {
			return {
                        upUrl : "",
				form: {
                              value1:"",
                              value2:"",
                              is_top:"",
                              is_fragile:"",
                              battery:"",
                              goods_weight:"",
                              goods_cpk:'',
                              goods_model:'',
                              goods_name:'',
                              goods_img:'',
                              goods_type:'',
                              material_code:"",
                              // connection_method:"",
                              goods_type1:'',
                              goods_type2:'',
                              goods_type3:'',
                              // wendor_id:'1',//
                              shop_price:'',
                              goods_unit:'',
                              supplier_id:"",
                              goods_attr:'',
                              is_sale:1,
                              goods_desc:'',
                              market_price:'',
                              internal_price:'',
                              distribution_price:'',
                              agent_a:'',
                              agent_b:'',

                        },
                        addFormRules: {
                              goods_name: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              is_top: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              goods_weight: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                               supplier_id: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              value1: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              value2: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              // connection_method:[{
                              //       required: true,
                              //       message: "",
                              //       trigger: "change"
                              // }],
                             
                              goods_type3: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              market_price: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              shop_price: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              internal_price: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              distribution_price: [{
                                    required: true,
                                    message: "",
                                    trigger: "change"
                              }],
                              agent_a: [{
                                    required: true,
                                    message: "A",
                                    trigger: "change"
                              }],
                              agent_b: [{
                                    required: true,
                                    message: "B",
                                    trigger: "change"
                              }]
                        },
				gtype2: false,
				gtype3: false,
				goodsType: [],
				goodsType2: [],
				goodsType3: [],
				specsState: false,
                        dialogVisible: false,
				specList: [{
					specCon: "",
					checked: false,
					btnstate: 0
				}],
				tables: [],
                        titleData: [],
                        market_value:'0',
                        shop_price:'0',
                        tableHeader:[],
                        tableData:[],
                        specs:[],
                        checkedArr:[],
                        checkRadio:-1,
                        firstLinkage:[],
                        first:'',
                        twoLinkage:[],
                        two:'',
                        threeLinkage:[],
                        three:'',
                        specsList:[],
                        gid:0,
                        editId:[],
                        file:{},
                        quillUpdateImg: false, // loadingfalse,
                        editorOption: {
                              placeholder: "",
                              theme: "snow", // or 'bubble'
                              placeholder: "",
                              modules: {
                                    ImageExtend: {
                                          loading: true,  //  
                                          name: 'img',  // 
                                          size: 10,  //  M1M = 1024kb
                                          action: resName.tarUrl(),  // , actionbase64
                                          // response 
                                          // {code: 200; data:{ url: 'baidu.com'}}
                                          //  return res.data.url
                                          response: (res) => {
                                                console.log(JSON.stringify(res));
                                                if(res.code === 1){
                                                      this.$message({
                                                            message: "",
                                                            type: "success"
                                                      });
                                                      return res.data
                                                }
                                          }
                                    },
                                    toolbar: {
                                          container: container,
                                          // container: "#toolbar",
                                          handlers: {
                                                'image': function(value) {
                                                      if (value) {
                                                            // input
                                                            document.querySelector(".avatar-uploader3 input").click();
                                                      } else {
                                                            this.quill.format("image", false);
                                                      }
                                                },
                                          }
                                    }
                              }
                        },
                        header: {
                        // token: sessionStorage.token
                        }, // token
                        supplier:[]
			}
            },
            created:function(){
                  this.upUrl = resName.tarUrl();
                  this.GetGoodsType();
                  this.getsupplier()
            },
            watch:{
                  tableData:{
                        handler:function(){
                              // console.log(1);
                        // this.packageData()
                        },
                        deep:true
                  },
                  market_value:{
                        handler:function(val){
                        this.modifyTable('market_value',val)
                        },
                  },
                  shop_price:{
                        handler:function(val){
                        this.modifyTable('shop_price',val)
                        },
                  },
                  specs:{
                        handler:function(){
                              this.generateList()
                        },
                        deep:true
                  },
            },
		mounted() {
			
		},
		methods: {
                  // 
                 getsupplier(){
                        resName
					.getSupplier()
					.then(res => {
						//
						if (res.code === 1) {
                                          this.supplier = res.data
						} else if (res.code === -2) {
							this.$message({
								message: res.msg,
								type: "error"
							});
						}
					})
					.catch(err => {
						//
						this.listLoading = false;
					});
                 },
                  // 
                  submitEdit:function(){
                        var _this = this
                        if(_this.$route.query.goods_id !== undefined){          //
                              _this.GoodsEdit(_this.$route.query.goods_id);
                        }else{       //
                        
                        _this.packageGoodsSpecData()
                        _this.packageSpecData()
                        _this.$refs.form.validate(valid => {
                              if (valid) {
                                    var params = _this.form;
                                    if(!_this.gtype3&&_this.gtype2){
                                          params.goods_type3 = _this.form.goods_type2;
                                    }else if(!_this.gtype2){
                                          params.goods_type2 = _this.form.goods_type1;
                                          params.goods_type3 = _this.form.goods_type1;
                                    }else{
                                          params.goods_type3 = _this.form.goods_type3.id;
                                    }
                                    if(_this.form.is_top == "" ){
                                          _this.form.is_top = 0
                                    }else if(_this.form.is_top == ""){
                                          _this.form.is_top = 1
                                    }
                                    if(_this.form.is_fragile == "" ){
                                          _this.form.is_fragile = 1
                                    }else if(_this.form.is_fragile == ""){
                                          _this.form.is_fragile = 2
                                    }
                                    if(_this.form.battery == "" ){
                                          _this.form.battery = 1
                                    }else if(_this.form.battery == ""){
                                          _this.form.battery = 2
                                    }
                                    params.goods_type = _this.form.goods_type3;
                                    resName
                                          .GoodsAdd(params)
                                          .then(res => {
                                                //
                                                if (res.data.code === 1) {
                                                      _this.$message({
                                                            message: "",
                                                            type: "success"
                                                      });
                                                      this.form = {
                                                            goods_cpk:'',
                                                            goods_model:'',
                                                            goods_name:'',
                                                            goods_img:'',
                                                            is_top:'',
                                                            is_fragile:"",
                                                            battery:"",
                                                            goods_weight:"",
                                                            goods_type:'',
                                                            goods_type1:'',
                                                            goods_type2:'',
                                                            goods_type3:'',
                                                            material_code:"",
                                                            // connection_method:'',
                                                            supplier_id:'',
                                                            shop_price:'',
                                                            goods_unit:'',
                                                            goods_attr:'',
                                                            is_sale:1,
                                                            goods_desc:'',
                                                            market_price:'',
                                                            internal_price:'',
                                                            distribution_price:'',
                                                            agent_a:'',
                                                            agent_b:''
                                                      };
                                                      
                                                      this.gtype2 = false;
                                                      this.gtype3 = false;

                                                      this.tableHeader = [];
                                                      this.tableData = [];
                                                      this.specs = [];
                                                      this.specsState = false;
                                                      this.$router.replace({ path: "/order/allCommodities" });
                                                } else{
                                                      this.$message({
                                                            message: res.msg,
                                                            type: "error"
                                                      });
                                                }
                                          })
                                          .catch(err => {
                                                //
                                                this.listLoading = false;
                                          });
                              }
                        })
                        }
                        
                  },
			//
			GetGoodsType() {
				let para = {
					id: ""
				};
				resName
					.GetGoodsType(para)
					.then(res => {
						//
						if (res.code === 1) {
                                          this.goodsType = res.data;
                                          _this.firstLinkage = res.data
						} else if (res.code === -7) {
							this.$message({
								message: res.msg,
								type: "error"
							});
						}
					})
					.catch(err => {
						//
						this.listLoading = false;
					});
			},
			selGoods(selVal) {
                        this.form.goods_type1 = selVal.id; 
                        this.goodsType2.length = 0;
                        this.goodsType3.length = 0;
                        this.form.value2 = "";
                        this.form.goods_type3 = "";
                        this.GetGoodsType2(selVal.id,"","");
                  },
                  GetGoodsType2(code,tar,tar3){
                        var _this = this;
                        let para = {
					id: code
                        };
				resName
					.GetGoodsType(para)
					.then(res => {
						//
						if (res.code === 1) {
                                          this.goodsType2 = res.data;
                                          if(res.data.length !== 0){
                                                this.gtype2 = true;
                                          }else{
                                                this.gtype2 = false;
                                          }
                                          
                                          if(tar !== "" && tar3 !== ""){
                                                let obj2 = {}; 
                                                obj2 = _this.goodsType2.find((item)=>{//userRoleList 
                                                      return item.id === tar;// 
                                                }); 
                                                _this.form.value2 = obj2; 
                                                this.GetGoodsType3(obj2.id,tar3);
                                          }
						} else if (res.code === -7) {
							this.$message({
								message: res.msg,
								type: "error"
							});
						}
					})
					.catch(err => {
						//
						this.listLoading = false;
					});
                  },
			selGoods2(selVal) {
                        this.form.goods_type2 = selVal.id; 
                        this.goodsType3.length = 0;
                        this.form.goods_type3 = "";
                        this.GetGoodsType3(selVal.id,"");
                  },
                  GetGoodsType3(code,tar){
                        var _this = this;
                        let para = {
					id: code
				};
				resName
					.GetGoodsType(para)
					.then(res => {
						//
						//console.log("3" + JSON.stringify(res));
						if (res.code === 1) {
							this.goodsType3 = res.data;
                                          if(res.data.length !== 0){
                                                this.gtype3 = true;
                                          }else{
                                                this.gtype3 = false;
                                          }
                                          if(tar !== ""){
                                                let obj3 = {}; 
                                                obj3 = _this.goodsType3.find((item)=>{//userRoleList 
                                                      return item.id === tar;// 
                                                }); 
                                                _this.form.goods_type3 = obj3;
                                                _this.form.goods_type = obj3;  
                                          }
						} else if (res.code === -7) {
							this.$message({
								message: res.msg,
								type: "error"
							});
						}
					})
					.catch(err => {
						//
					});
                  },
			selGoods3(selVal) {
                        var _this = this;
                        
				let pramas = {
					goods_type: selVal.id
				};
				resName
					.GetSpec(pramas)
					.then(res => {
                                    //console.log("" + JSON.stringify(res));
                                    //console.log(_this.form.goods_type1,_this.form.goods_type2,_this.form.goods_type3);
						if (res.code === 1) {
							if (res.data.length !== 0) {
                                                this.specsState = true;
                                                 _this.specsList = res.data;
                                                _this.specsSwitch()
							} else {
								this.specsState = false;
							}
						} else if (res.code === -7) {
							_this.specsState = false;
							_this.$message({
								message: res.msg,
								type: "error"
							});
						}
					})
					.catch(err => {
						//
					});
                  },
                  specsSwitch:function(){
                        var _this = this
                        var arr = []
                        for (let index = 0; index < _this.specsList.length; index++) {
                        const element = _this.specsList[index];
                        arr.push({
                              id:_this.specsList[index].cat_id,
                              title:_this.specsList[index].cat_name,
                              is_img:_this.specsList[index].is_img == 1 ? true : false,
                        })
                        }
                        _this.specsList = arr
                        _this.init()
                  },
                  // 
                  init:function(){
                        var _this = this
                        _this.specs =[]
                        var obj = _this.specsList
                        for (let index = 0; index < obj.length; index++) {
                        this.specs.push({
                              id:obj[index]['id'],
                              title:obj[index]['title'],
                              is_img:obj[index]['is_img'],
                              // fields:obj[index]['fields'],
                              _list:[{
                                    is_checked:false,//
                                    value:'',//
                                    img:'',//
                              }]
                        })
                        }
                  },
                  // 
                  newlyAdded:function(val){
                        var obj = {
                        is_checked:false,//
                        value:'',//
                        img:'',//
                        }
                        this.specs[val]._list.push(obj)
                  },
                  // 
                  newlyDel:function(val,i){
                        this.specs[val]._list.splice(i,1)
                  },
                  cartesianProductOf:function(){
                        return Array.prototype.reduce.call(arguments,function(a, b) {
                              var ret = [];
                              
                              a.forEach(function(a) {
                                    b.forEach(function(b) {
                                          ret.push(a.concat([b]));
                                    });
                              });
                              return ret;
                        }, [[]])
                  },
                  // 
                  generateList:function(){
                        //console.log(JSON.stringify(this.specs));
                        var _this = this;
                        var specCols = {};
                        this.tableHeader =[];
                        this.checkedArr =[];
                        for (var index = 0; index < this.specs.length; index++) {
                              for (var i = 0; i < this.specs[index]._list.length; i++) {
                                    //
                                    if(this.specs[index]._list[i].is_checked){
                                          var catId = this.specs[index].title
                                          if(!specCols[catId]){
                                          specCols[catId] = [];
                                          specCols[catId].push(this.specs[index]._list[i].value);
                                          }else{
                                          specCols[catId].push(this.specs[index]._list[i].value);
                                          }
                                    }
                              }
                        }
                        
                        for(var i in specCols){
                              this.tableHeader.push(i);
                              this.checkedArr.push(specCols[i])
                        }
                        if(this.checkedArr.length != 0){
                              
                              this.tableData = this.cartesianProductOf.apply(_this,this.checkedArr);
                              
                              // this.tableData = this.checkedArr;
                              
                        }
                        
                        for(var i in this.tableData){
                              this.tableData[i].market_value ='0'
                              this.tableData[i].shop_price ='0'
                        }
                        
                        // console.log(JSON.stringify(this.tableData));
                        this.packageSpecData()
                        this.packageGoodsSpecData()
                  },
                  modifyTable:function(key,val){
                        
                        for(var i in this.tableData){
                              this.tableData[i][key] = val
                        }

                        
                  },
                  //
                  packageSpecData:function(){
                        var arr=[]
                        var specs={}
                        var spec_ids = []
                        for (let index = 0; index < this.specs.length; index++) {
                              arr=[]
                              for (let i = 0; i < this.specs[index]._list.length; i++) {
                                    if (this.specs[index]._list[i].is_checked){
                                          arr.push({'spec_name':this.specs[index]._list[i].value,'url':this.specs[index]._list[i].img})
                                          // console.log(JSON.stringify(this.specs[index]));
                                    }
                              }
                              
                              if(arr.length != 0){
                                    specs[this.specs[index].title] = arr
                                    spec_ids.push(this.specs[index].id)
                              }
                        }
                        this.form.spec = specs
                        this.form.specs = specs
                        // this.form.specs = spec_ids
                        //console.log(JSON.stringify(this.form.specs));
                        
                  },
                  //
                  packageGoodsSpecData:function(){
                        var catId=[]
                        for (let index = 0; index < this.specs.length; index++) {
                              var res = this.tableHeader.indexOf(this.specs[index].title)
                              
                              if(res!=-1){
                                    // catId.push(this.specs[index].id)
                                    catId.push(this.specs[index].title)
                              }
                        }
                        
                        var goodsArr = {}
                        var goodsSpec =[]
                        
                        for (let index = 0; index < this.tableData.length; index++) {
                              var res = this.tableData[index];
                              
                              goodsArr={}
                              for(var item in res){
                                    if(catId[item]){
                                          Object.assign(goodsArr,{[catId[item]]:res[item]})
                                    }else{
                                          Object.assign(goodsArr,{[item]:res[item]})
                                    }
                                   
                                    if(index == this.checkRadio){
                                          Object.assign(goodsArr,{'is_default':1})
                                    }else{
                                          Object.assign(goodsArr,{'is_default':0})
                                    }
                              }
                              goodsSpec[index] = goodsArr
                        }

                        if(this.editId.length !== 0){
                              for(var j = 0; j < this.editId.length; j++){
                                    goodsSpec[j].id = this.editId[j];
                              }
                        }
                        // console.log(JSON.stringify(goodsSpec));
                        this.form.goods_spec = goodsSpec
                        
                  },
			submit() {
				// console.log(this.$refs.text.value);
			},
                  //
                  handleAvatarSuccess(res, file) {
                        this.form.goods_img = URL.createObjectURL(file.raw);
                  },
                  uploadImg(event,cat,index) {
                        var _this = this;
                        // console.log(cat);
                        var fileObj = event.target.files[0]; //inputfile
                        let param = new FormData(); // form
                        param.append("action", "UploadVMKImagePath");
                        param.append('file', fileObj);//
                        
                        resName.uploadImg(param).then(res => {
                              // console.log(""+JSON.stringify(res));
                              if(res.data.code === 1){
                                  _this.specs[cat]._list[index].img = res.data.data
                                  _this.specs[cat]._list[index].is_img = true
                              }
                              
                        }).catch(err => {
                              //
                              // console.log(""+JSON.stringify(err));
                        });
                  },
                  // 
                  handleChange(file) {
                        this.file = file.raw
                  },
                  uploadMainImg(){
                        var _this = this;
                        // 
                        let form = new FormData();
                        //  
                        form.append("file",this.file)
                        form.append("action", "UploadVMKImagePath");
                        
                        resName.uploadImg(form).then(res => {
                              if(res.data.code === 1){
                                    console.log(form);
                                    if(_this.form.goods_img !== ""){
                                          let imgUrl = _this.form.goods_img;
                                          resName.deleOldImg(imgUrl);
                                    }
                                  _this.form.goods_img = res.data.data
                              }
                              
                        }).catch(err => {
                              //
                              // console.log(""+JSON.stringify(err));
                        });
                  },

                  onEditorBlur() {
                              //
                        },
                        onEditorFocus() {
                              //
                        },
                        onEditorChange() {
                              //
                              this.$emit("input", this.content);
                        },

                        // 
                        beforeUpload() {
                              // loading
                              this.quillUpdateImg = true;
                        },

                        uploadSuccess(res, file) {
                              // console.log(JSON.stringify(res));
                              // res
                              // 
                              let quill = this.$refs.myQuillEditor.quill;
                              // 
                              if (res.code == 1) {
                                    // 
                                    let length = quill.getSelection().index;
                                    //   res.url
                                    quill.insertEmbed(length, "image", res.data);
                                    // 
                                    quill.setSelection(length + 1);
                              } else {
                                    this.$message.error("");
                              }
                              // loading
                              this.quillUpdateImg = false;
                        },
                        // 
                        uploadError() {
                              // loading
                              this.quillUpdateImg = false;
                              this.$message.error("");
                        }
		}
      };
      
      function multiply(arr) {
            return arr.reduce(function(a, b) {
                return a * b;        //return a + b return a - b
            })
      }
</script>

<style scoped lang="less">
	ul,
	li {
		margin: 0;
		padding: 0;
		list-style: none;
	}
      .goodsIns .el-form-item{
            width: 50%;
            float: left;
      }
	.ql-snow .ql-picker {
