	}
      .specItem .el-input{
            width: 200px;
      }
      .specItem{
            margin-bottom:10px;
      }
      .el-checkbox{
            margin-right:15px;
      }
      .am-table{
            width:100%;
            border-collapse:collapse;
      }
      .am-table .el-input{
            width: 120px;
      }
      .am-table>thead>tr>th {
            vertical-align: bottom;
