<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="search">
				<el-form-item class="left" style="float:left;">
					<el-button type="primary" icon="el-icon-plus" size="mini" @click="AddtoStorage"></el-button>
				</el-form-item>
				<el-form-item class="left" style="float:left;">
					<!-- <el-button type="primary" icon="el-icon-plus" size="mini" @click="AddtoStorage"></el-button> -->
					<el-select v-model="value1" multiple placeholder="" size="small">
						<el-option
						v-for="item in options"
						:key="item.value"
						:label="item.label"
						:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
		</el-col>
	<!--  -->
		<el-table
			:data="tableData"
			style="width: 99.9%;margin-bottom: 20px;"
			text-align="center"
			border
			v-loading="listLoading">
			<el-table-column prop="name" label="" align="center" sortable></el-table-column>
			<el-table-column prop="name" label="" align="center" sortable></el-table-column>
			<el-table-column prop="name" min-width="120" align="center" label="" sortable></el-table-column>
			<el-table-column prop="name" align="center" label="" sortable></el-table-column>
			<el-table-column prop="name" align="center" label="" sortable></el-table-column>
			<el-table-column prop="name" align="center" label="" sortable></el-table-column>
			<el-table-column prop="name" label="" align="center" sortable></el-table-column>
			<el-table-column prop="name" min-width="120" align="center" label="" sortable></el-table-column>
			<el-table-column prop="mac" align="center" label="" sortable></el-table-column>
			<el-table-column label="" width="250" align="center">
				<template slot-scope="scope">
					<el-button
						type="success"
						@click="handleDetail(scope.$index, scope.row)"
						size="mini"
						plain
					></el-button>
					<el-button type="success" @click="handleEdit(scope.$index, scope.row)" size="mini" plain></el-button>
					<el-button type="danger" @click="handleDel(scope.$index, scope.row)" size="mini" plain></el-button>
				</template>
			</el-table-column>
		</el-table>
		<!--  -->
		<el-col :span="24" class="toolbar">
			<el-pagination
				background
				@size-change="handleSizeChange"
				@current-change="handleCurrentChange"
				:page-sizes="[10, 20, 30, 50]"
				:page-size="pageSize"
				layout="sizes, prev, pager, next"
				:total="total">
			</el-pagination>
		</el-col>
	<!---->
		<el-dialog title="" :visible.sync="addwarehousVisible" :close-on-click-modal="false" :show-close="true">
			<el-form :model="addwarehousForm" :rules="addFormRules" ref="addwarehousForm" class="creactivit" :hide-required-asterisk="false" label-width="100px">
				<el-form-item label="" prop="order_no">
				<el-input placeholder="" v-model="addwarehousForm.order_no" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="order_no">
				<el-input placeholder="" v-model="addwarehousForm.order_no" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="order_no">
				<el-input placeholder="" v-model="addwarehousForm.order_no" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="order_no">
					<el-select v-model="value1" multiple placeholder="">
						<el-option
						v-for="item in options"
						:key="item.value"
						:label="item.label"
						:value="item.value">
						</el-option>
					</el-select>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button size="small" @click.native="addwarehousVisible = false " @click="resetFields('addwarhousForm')"></el-button>
				<el-button size="small" type="primary" @click.native="addwarehousSubmit"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script>
	export default {
		data() {
			return {
				addwarehousVisible: false, //
				addLoading: false,
				addFormRules: {
					order_no: [
					{
						required: true,
						message: "",
						trigger: "change"
					}
					]
				},
				//
				addwarehousForm: {
					project_id: "",
					duotel_id: "",
					order_no:"",
				},
				options: [{
					value: '1',
					label: ''
					}, {
					value: '2',
					label: ''
					}, {
					value: '3',
					label: ''
					}, {
					value: '4',
					label: ''
					}, {
					value: '5',
					label: ''
				}],
				value: '',
				tableData: [{
					date: '2016-05-02',
					name: '',
					address: ' 1518 '
				}],
				page: 1,
				pageSize: 10,
				total: 0,
				listLoading:false,
				time1:"",
				time2:"",
				value1:"",
				search:{
					name:""
				}
			};
		},
		created:function(){
			
		},
		methods:{
			AddtoStorage(){
				this.$router.push({path:"/office/inventory/AddtoStorage"})
			},
			formatter:function(){
				console.log("")
			},
			// 
			handleSizeChange(val) {
			//
				this.pageSize = val;
				this.formatter();
			},
			handleCurrentChange(val) {
				this.page = val;
				this.formatter();
			},
			chooseTimeRange:function(){

			},
			// 
			handleDetail:function(){
				this.$router.push({path:"/office/inventory/DetailStorage"})
			},
			// 
			handleEdit:function(){
				this.$router.push({path:"/office/inventory/EditStorage"})
			},
			// 
			addwarehousSubmit:function(){
			},
			// 
			handleAdd:function(){
				this.addwarehousVisible = true;
			}
		}
	}
</script>

<style lang="css" scoped>
.toolbar .el-form-item .right {
  float: right;
}
.toolbar .el-form-item .left {
  float: left;
}
.row-bg {
  padding: 12px 10px;
  background: #fff;
  margin-bottom: 20px;
}

.input-with-select {
  float: right;
}
.el-table__row td {
  text-align: center;
}
.has-gutter th {
  text-align: center;
}
.toolbar,
.el-table {
  background: #fff;
}
.toolbar {
  margin-bottom: 15px;
}

</style>


