import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

const state = {
	collapsed: false, //
	defaultActiveIndex: '/', //
	topNavState: 'home',
      leftNavState: '',
      leftNavLevel:[],
	role: {
		admin: '',
		ceshi: ''
	},
	userInfo: {}
}

export default new Vuex.Store({
	state
})



