<template>
  <section>
    <!--  -->
    <!---->
    <!--  -->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="search">
        <el-form-item class="right">
          <el-button type="primary" size="mini" icon="el-icon-search" v-on:click="getlist"></el-button>
        </el-form-item>
        <el-form-item class="right">
          <el-input v-model="search.waName" size="mini" placeholder=""></el-input>
        </el-form-item>
        <el-form-item class="left">
          <el-button type="primary" size="mini" icon="el-icon-plus" @click="addList"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!--  -->
    <el-table
      border
      ref="multipleTable"
      :data="tableData"
      align="center"
      tooltip-effect="dark"
      style="width: 99.9%;margin-bottom: 20px;"
      v-loading="listLoading">
      <el-table-column prop="id" label="ID" align="center" width="80" sortable></el-table-column>
      <el-table-column label="" width="120" sortable align="center">
        <template slot-scope="scope">
          <p
            v-for="item in commonsType"
            v-if="scope.row.msg_type == item.class_code"
          >{{item.class_value}}</p>
        </template>
      </el-table-column>
      <el-table-column label="" width="120" sortable align="center">
        <template slot-scope="scope">
          <p v-for="item in commonsData"
            v-if="scope.row.tpl_type == item.class_code"
          >{{item.class_value}}</p>
        </template>
      </el-table-column>
      <el-table-column prop="title" label="" align="center" sortable></el-table-column>
      <el-table-column prop="desc" label="" align="center" sortable></el-table-column>
      <el-table-column label="" align="center" sortable>
        <template slot-scope="scope">
          <p v-html="scope.row.content" style="max-height:86px;overflow:hidden"></p>
        </template>
      </el-table-column>
      <el-table-column label="" align="center" width="170">
        <template slot-scope="scope">
          <el-button
            type="success"
            plain
            size="mini"
            @click="handleEdit(scope.$index, scope.row)"
          ></el-button>
          <el-button
            size="mini"
            plain
            type="danger"
            @click="handleDelete(scope.$index, scope.row)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      :visible.sync="editorialActivities"
      :show-close="true"
      :close-on-click-modal="false">
      <el-form
        :model="editForm"
        :rules="editFormRules"
        ref="editForm"
        class="creactivit"
        :hide-required-asterisk="false"
        label-width="100px"
      >
        <el-form-item label="" prop="msg_type">
          <template>
            <el-select placeholder="" v-model="editForm.msg_type" style="width:100%;">
              <el-option
                v-for="item in commonsType"
                :key="item.class_code"
                :value="item.class_value"
              ></el-option>
            </el-select>
          </template>
        </el-form-item>
        <el-form-item label="" prop="tpl_type">
          <el-select v-model="editForm.tpl_type" placeholder="" style="width:100%;">
            <el-option v-for="item in commonsData" :key="item.class_code" :value="item.class_value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input placeholder="" v-model="editForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="code" prop="code">
          <el-input placeholder="code" v-model="editForm.code" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- <el-input placeholder="" type="textarea" v-model="editForm.content" auto-complete="off"></!-->
         <el-upload
              class="avatar-uploader3"
              :action="upUrl"
              name="img"
              :headers="header"
              :show-file-list="false"
              :on-success="uploadSuccess"
              :on-error="uploadError"
              :before-upload="beforeUpload">
              </el-upload>

              <quill-editor 
              class="editor"
              v-model="editForm.content"
              ref="myQuillEditor" 
              :options="editorOption" 
              @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
              @change="onEditorChange($event)">
              </quill-editor>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input placeholder="" v-model="editForm.desc" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click.native="editorialActivities = false"></el-button>
        <el-button size="small" type="primary" @click.native="editSubmit"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-dialog
      title=""
      :visible.sync="addFormVisible"
      :show-close="true"
      :close-on-click-modal="false">
      <el-form
        :model="addForm"
        :rules="addFormRules"
        ref="addForm"
        class="creactivit"
        :hide-required-asterisk="false"
        label-width="100px"
      >
        <el-form-item label="" prop="msg_type">
          <template>
            <el-select placeholder="" v-model="addForm.msg_type" style="width:100%;">
              <el-option
                v-for="item in commonsType"
                :key="item.class_code"
                :value="item.class_value"
              ></el-option>
            </el-select>
          </template>
        </el-form-item>
        <el-form-item label="" prop="tpl_type">
          <el-select v-model="addForm.tpl_type" placeholder="" style="width:100%;">
            <el-option v-for="item in commonsData" :key="item.class_code" :value="item.class_value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="title">
          <el-input placeholder="" v-model="addForm.title" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="code" prop="code">
          <el-input placeholder="code" v-model="addForm.code" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="content">
          <!-- <el-input type="textarea" placeholder="" v-model="addForm.content" auto-complete="off"></el-input> -->
        <el-upload
              class="avatar-uploader3"
              :action="upUrl"
              name="img"
              :headers="header"
              :show-file-list="false"
              :on-success="uploadSuccess"
              :on-error="uploadError"
              :before-upload="beforeUpload">
              </el-upload>

              <quill-editor 
              class="editor"
              v-model="addForm.content"
              ref="myQuillEditor" 
              :options="editorOption" 
              @blur="onEditorBlur($event)" @focus="onEditorFocus($event)"
              @change="onEditorChange($event)">
              </quill-editor>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input placeholder="" v-model="addForm.desc" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click="resetFields('addForm')"></el-button>
        <el-button size="small" type="primary" @click.native="addSubmit"></el-button>
      </div>
    </el-dialog>
  </section>
</template>
<style>
.ql-container.ql-snow {
  box-sizing: border-box;
  font-family: Helvetica, Arial, sans-serif;
  font-size: 13px;
  height: auto;
      min-height:200px;
      max-height:350px;
  margin: 0px;
  position: relative;
}
.ql-editor{
      height: auto;
      min-height:200px;
      max-height:350px;
}
</style>
<script>
import "quill/dist/quill.core.css";
	import "quill/dist/quill.snow.css";
      import "quill/dist/quill.bubble.css";
      import axios from 'axios';
	import {
		quillEditor,Quill
      } from "vue-quill-editor";
      import {container, ImageExtend} from 'quill-image-extend-module'
      if (!Quill.imports['modules/ImageExtend']) {
            // Register only if not exists - 
            Quill.register('modules/ImageExtend', ImageExtend);
      }
import { resName } from "../../api/api";
export default {
      components: {
			quillEditor
		},
  data() {
    return {
      // rowContent:false,
      upUrl : "",
      addFormVisible: false,
      search: {
        waName: ""
      },
	  tableData: [],
      total: 0,
      page: 1,
      pageSize: 10,
      listLoading: false,
      editorialActivities: false,
      commonsData: [],
      commonsType: [],
      editForm: {
        id: "",
        tpl_type: "",
        msg_type: "",
        title: "",
        code: "",
        content: "",
        desc: ""
      },
      editFormRules: {
        tpl_type: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        msg_type: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        title: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        code: [
          {
            required: true,
            message: "code",
            trigger: "change"
          }
        ],
        content: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        desc: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
      addForm: {
        id: "",
        tpl_type: "",
        msg_type: "",
        title: "",
        code: "",
        content: "",
        desc: ""
      },
      addFormRules: {
        tpl_type: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        msg_type: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        code: [
          {
            required: true,
            message: "code",
            trigger: "change"
          }
        ],
        content: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        desc: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ]
      },
       quillUpdateImg: false, // loadingfalse,
                editorOption: {
                        placeholder: "",
                        theme: "snow", // or 'bubble'
                        placeholder: "",
                        modules: {
                            ImageExtend: {
                                    loading: true,  //  
                                    name: 'img',  // 
                                    size: 10,  //  M1M = 1024kb
                                    action: resName.tarUrl(),  // , actionbase64
                                    // response 
                                    // {code: 200; data:{ url: 'baidu.com'}}
                                    //  return res.data.url
                                    response: (res) => {
                                        console.log(JSON.stringify(res));
                                        if(res.code === 1){
                                                this.$message({
                                                    message: "",
                                                    type: "success"
                                                });
                                                return res.data
                                        }
                                    }
                            },
                            toolbar: {
                                    container: container,
                                    // container: "#toolbar",
                                    handlers: {
                                        'image': function(value) {
                                                if (value) {
                                                    // input
                                                    document.querySelector(".avatar-uploader3 input").click();
                                                } else {
                                                    this.quill.format("image", false);
                                                }
                                        },
                                    }
                            }
                        }
                },
    header: {
                // token: sessionStorage.token
                }
    
    };
  },
  created() {
    this.getlist();
  },
  methods: {
    getlist() {
      var params = {
        content: this.search.waName,
        tpl_type: "",
	  	msg_type: "",
	  	page: this.page,
        per_page: this.pageSize
      };
      this.listLoading = true;
      resName
        .messageTplList(params)
        .then(res => {
          if (res.code === 1) {
			this.total = res.data.total;
            this.tableData = res.data.data;
            this.listLoading = false;
          }
        })
        .catch(err => {
          //
          this.listLoading = false;
          return false;
        });
      // 
      let param = {
        class_type: "tpl_type"
      };
      resName
        .webgetcommonsdata(param)
        .then(res => {
          if (res.code === 1) {
            this.commonsData = res.data;
          }
        })
        .catch(err => {
          //
          return false;
        });
      // 
      let para = {
        class_type: "msg_type"
      };
      resName
        .webgetcommonsdata(para)
        .then(res => {
          if (res.code === 1) {
            this.commonsType = res.data;
          }
        })
        .catch(err => {
          //
          return false;
        });
    },
    // 
    resetFields(addForm) {
      this.$refs[addForm].resetFields();
      this.addFormVisible = false;
    },
    // 
    addList() {
      this.addFormVisible = true;
    },
    // 
    addSubmit() {
      this.$refs.addForm.validate(valid => {
          for (var i in this.commonsType) {
			if (this.addForm.msg_type == this.commonsType[i].class_value) {
				var msgData = this.commonsType[i].class_code;
				}
			}
			for (var i in this.commonsData) {
				if (this.addForm.tpl_type == this.commonsData[i].class_value) {
					var tplType = this.commonsData[i].class_code;
				}
			}
          var params = {
            tpl_type: tplType,
            msg_type: msgData,
            code: this.addForm.code,
            content: this.addForm.content,
            desc: this.addForm.desc
          };
          resName
            .messageTplAdd(params)
            .then(res => {
              //
              if (res.data.code === 1) {
                this.$message({
                  message: "",
                  type: "success"
                });
                this.$refs["addForm"].resetFields();
                this.addFormVisible = false;
                this.getlist();
              } else if (res.code === -2) {
                this.$message({
                  message: res.msg,
                  type: "error"
                });
              }
            })
            .catch(err => {
              //
            });
	  })
      
    },
    // 
    handleDelete(index, row) {
	  	this.$confirm("?", "", {
			type: "warning"
			}).then(() => {
			var del = {
				id: row.id
			};
			resName.messageTplDel(del).then(res => {
				//
				if (res.data.code === 1) {
				this.$message({
					message: "",
					type: "success"
				});
				this.getlist();
				} else if (res.code === -2) {
				this.$message({
					message: res.msg,
					type: "error"
				});
				}
			})
			})
	},
    // 
    handleEdit(index, row) {
	    this.editorialActivities = true;
      for (var i in this.commonsType) {
        if (row.msg_type == this.commonsType[i].class_code) {
          var msgType = this.commonsType[i].class_value;
        }
      }
      for (var i in this.commonsData) {
        if (row.tpl_type == this.commonsData[i].class_code) {
          var tplData = this.commonsData[i].class_value;
        }
      }
      this.editForm = {
        id: row.id,
        tpl_type: tplData,
        msg_type: msgType,
        title: row.title,
        code: row.code,
        content: row.content,
        desc: row.desc
	  };
	},
    editSubmit() {
      this.$refs.editForm.validate(valid => {
        for (var i in this.commonsType) {
          if (this.editForm.msg_type == this.commonsType[i].class_value) {
            var msgData = this.commonsType[i].class_code;
          }
        }
        for (var i in this.commonsData) {
          if (this.editForm.tpl_type == this.commonsData[i].class_value) {
            var tplType = this.commonsData[i].class_code;
          }
        }
        if (valid) {
          var pramas = {
            id: this.editForm.id,
            msg_type: msgData,
            tpl_type: tplType,
            title: this.editForm.title,
            code: this.editForm.code,
            content: this.editForm.content,
            desc: this.editForm.desc
          };
          resName
            .meTplEdit(pramas)
            .then(res => {
              //
              if (res.data.code === 1) {
                this.$message({
                  message: "",
                  type: "success"
                });
                this.editorialActivities = false;
                this.$refs["editForm"].resetFields();
                this.getlist();
              } else if (res.code === -2) {
                this.$message({
                  message: res.msg,
                  type: "error"
                });
              }
            })
            .catch(err => {
              //
            });
        }
      });
    },
	handleSizeChange(val) {
	  //
      this.pageSize = val;
	  this.getlist();
	},
	handleCurrentChange(val) {
	  this.page = val;
      this.getlist();
  },
   onEditorBlur() {
          //
    },
    onEditorFocus() {
          //
    },
    onEditorChange() {
          //
          this.$emit("input", this.content);
    },

    // 
    beforeUpload() {
          // loading
          this.quillUpdateImg = true;
    },

    uploadSuccess(res, file) {
          // console.log(JSON.stringify(res));
          // res
          // 
          let quill = this.$refs.myQuillEditor.quill;
          // 
          if (res.code == 1) {
                // 
                let length = quill.getSelection().index;
                //   res.url
                quill.insertEmbed(length, "image", res.data);
                // 
                quill.setSelection(length + 1);
          } else {
                this.$message.error("");
          }
          // loading
          this.quillUpdateImg = false;
    },
    // 
    uploadError() {
          // loading
          this.quillUpdateImg = false;
          this.$message.error("");
    }
  }
};
</script>

<style lang="less" scoped>
.rowContent{
    width: 700px;
    height: auto;
    left: 50%;
    background: rgba(68, 68, 68, 0.6);
    display: block;
    position: absolute;
    top: 111px;
    transform: translateX(-50%);
    color:white;
    text-align:center;
    line-height: 20px;
}
.toolbar,
.el-table {
  background: #fff;
}
.toolbar {
  margin-bottom: 15px;
}
.el-form-item.right {
  float: right;
}
.el-form-item.left {
  float: left;
}
 /**/
      .editor {
            line-height: normal !important;
      }
      .ql-snow .ql-tooltip[data-mode=link]::before {
            content: ":";
      }
      .ql-snow .ql-tooltip.ql-editing a.ql-action::after {
            border-right: 0px;
            content: '';
            padding-right: 0px;
      }

      .ql-snow .ql-tooltip[data-mode=video]::before {
            content: ":";
      }

      .ql-snow .ql-picker.ql-size .ql-picker-label::before,
      .ql-snow .ql-picker.ql-size .ql-picker-item::before {
            content: '14px';
      }
      .ql-snow .ql-picker.ql-size .ql-picker-label[data-value=small]::before,
      .ql-snow .ql-picker.ql-size .ql-picker-item[data-value=small]::before {
            content: '10px';
      }
      .ql-snow .ql-picker.ql-size .ql-picker-label[data-value=large]::before,
      .ql-snow .ql-picker.ql-size .ql-picker-item[data-value=large]::before {
            content: '18px';
      }
      .ql-snow .ql-picker.ql-size .ql-picker-label[data-value=huge]::before,
      .ql-snow .ql-picker.ql-size .ql-picker-item[data-value=huge]::before {
            content: '32px';
      }

      .ql-snow .ql-picker.ql-header .ql-picker-label::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item::before {
            content: '';
      }
      .ql-snow .ql-picker.ql-header .ql-picker-label[data-value="1"]::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item[data-value="1"]::before {
            content: '1';
      }
      .ql-snow .ql-picker.ql-header .ql-picker-label[data-value="2"]::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item[data-value="2"]::before {
            content: '2';
      }
      .ql-snow .ql-picker.ql-header .ql-picker-label[data-value="3"]::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item[data-value="3"]::before {
            content: '3';
      }
      .ql-snow .ql-picker.ql-header .ql-picker-label[data-value="4"]::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item[data-value="4"]::before {
            content: '4';
      }
      .ql-snow .ql-picker.ql-header .ql-picker-label[data-value="5"]::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item[data-value="5"]::before {
            content: '5';
      }
      .ql-snow .ql-picker.ql-header .ql-picker-label[data-value="6"]::before,
      .ql-snow .ql-picker.ql-header .ql-picker-item[data-value="6"]::before {
            content: '6';
      }

      .ql-snow .ql-picker.ql-font .ql-picker-label::before,
      .ql-snow .ql-picker.ql-font .ql-picker-item::before {
            content: '';
      }
      .ql-snow .ql-picker.ql-font .ql-picker-label[data-value=serif]::before,
      .ql-snow .ql-picker.ql-font .ql-picker-item[data-value=serif]::before {
            content: '';
      }
      .ql-snow .ql-picker.ql-font .ql-picker-label[data-value=monospace]::before,
      .ql-snow .ql-picker.ql-font .ql-picker-item[data-value=monospace]::before {
            content: '';
      }
      .avatar-uploader3{
            display: none;
      }
</style>




