<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true">
        <el-form-item class="left">
          <el-button type="primary" size="mini" icon="el-icon-plus" @click="addSupplier"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table
      :data="starLevel"
      highlight-current-row
      text-align="center"
      v-loading="listLoading"
      style="width: 99.9%;"
      border
    >
      <el-table-column prop="id" label="id" align="center"></el-table-column>
      <el-table-column prop="supplier_name" label="" align="center"></el-table-column>
      <el-table-column prop="supplier_address" label="" align="center"></el-table-column>
      <el-table-column prop="supplier_contact_people" label="" align="center"></el-table-column>
      <el-table-column prop="supplier_phone" label="" align="center"></el-table-column>
      <el-table-column label="" width="250" align="center">
        <template slot-scope="scope">
          <el-button
            type="success"
            @click="handleEdit(scope.$index, scope.row)"
            size="mini"
            plain
          ></el-button>
          <el-button type="danger" @click="handleDel(scope.$index, scope.row)" size="mini" plain></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-dialog
      title=""
      height="30%"
      :visible.sync="editFormVisible"
      :close-on-click-modal="false"
    >
      <el-form
        :model="editForm"
        :hide-required-asterisk="true"
        :rules="editFormRules"
        ref="editForm"
        label-width="140px"
      >
        <el-form-item label="" prop="supplier_name">
          <el-input placeholder="" v-model="editForm.supplier_name" class="input-with-select"></el-input>
        </el-form-item>
        <el-form-item label="" prop="supplier_address">
          <el-input placeholder="" v-model="editForm.supplier_address" class="input-with-select"></el-input>
        </el-form-item>
        <el-form-item label="" prop="supplier_contact_people">
          <el-input placeholder="" v-model="editForm.supplier_contact_people" class="input-with-select"></el-input>
        </el-form-item>
        <el-form-item label="" prop="supplier_phone">
          <el-input placeholder="" v-model="editForm.supplier_phone" class="input-with-select"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click.native="editFormVisible = false"></el-button>
        <el-button size="small" type="primary" @click.native="editSubmit"></el-button>
      </div>
    </el-dialog>
    <!--  -->
    <el-dialog
      title=""
      height="30%"
      :visible.sync="addFormVisible"
      :close-on-click-modal="false"
    >
      <el-form
        :model="addForm"
        :hide-required-asterisk="true"
        :rules="addFormRules"
        ref="addForm"
        label-width="140px"
      >
        <el-form-item label="" prop="supplier_name">
          <el-input placeholder="" v-model="addForm.supplier_name" class="input-with-select"></el-input>
        </el-form-item>
        <el-form-item label="" prop="supplier_address">
          <el-input placeholder="" v-model="addForm.supplier_address" class="input-with-select"></el-input>
        </el-form-item>
        <el-form-item label="" prop="supplier_contact_people">
          <el-input placeholder="" v-model="addForm.supplier_contact_people" class="input-with-select"></el-input>
        </el-form-item>
        <el-form-item label="" prop="supplier_phone">
          <el-input placeholder="" v-model="addForm.supplier_phone" class="input-with-select"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button size="small" @click.native="addFormVisible = false"></el-button>
        <el-button size="small" type="primary" @click.native="addSubmit"></el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
import { resName } from "../../../api/api";
export default {
  data() {
    return {
      listLoading: false,
      starLevel: [],
      addFormVisible: false,
      editFormVisible: false,
      addForm: {
        supplier_name:"",
        supplier_address:"",
        supplier_contact_people:"",
        supplier_phone: ""
      },
      addFormRules: {
        supplier_name: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        supplier_address: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        supplier_contact_people: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        supplier_phone: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
      },
      editForm: {
        id: "",
        supplier_name:"",
        supplier_address:"",
        supplier_contact_people:"",
        supplier_phone: ""
      },
      editFormRules: {
        supplier_name: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        supplier_address: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        supplier_contact_people: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
        supplier_phone: [
          {
            required: true,
            message: "",
            trigger: "change"
          }
        ],
      },
      
    };
  },
  created() {
    this.supplierdata();
  },
  methods: {
    //   
    supplierdata() {
      let params = {
        
      };
      resName
        .supplierList(params)
        .then(res => {
          //
          if (res.code === 1) {
            this.starLevel = res.data;
          }
        })
        .catch(err => {
          //
          return false;
        });
    },
    // 
    addSupplier() {
      this.addFormVisible = true;
    },
    // 
    addSubmit() {
        this.$refs.addForm.validate(valid => {
           if (valid) {
      let params = {
        supplier_name: this.addForm.supplier_name,
        supplier_address: this.addForm.supplier_address,
        supplier_contact_people:this.addForm.supplier_contact_people,
        supplier_phone:this.addForm.supplier_phone
      };
      resName
        .addSuppliers(params)
        .then(res => {
          //
          if (res.data.code === 1) {
            this.$message({
              message: "",
              type: "success"
            });
            this.addFormVisible = false;
            this.supplierdata();
          } else if (res.data.code === -2) {
            this.$message({
              message: res.msg,
              type: "error"
            });
          }
        })
        .catch(err => {
          //
          return false;
        });
           }
        })
    },
    // 
    handleEdit(index, row) {
      this.editFormVisible = true;
      this.editForm.id = row.id;
      this.editForm.supplier_name = row.supplier_name;
      this.editForm.supplier_address = row.supplier_address;
      this.editForm.supplier_contact_people = row.supplier_contact_people;
      this.editForm.supplier_phone = row.supplier_phone;
    },
    // 
    editSubmit() {
        this.$refs.editForm.validate(valid => {
           if (valid) {
            let params = {
                id: this.editForm.id,
                supplier_name: this.editForm.supplier_name,
                supplier_address: this.editForm.supplier_address,
                supplier_contact_people:this.editForm.supplier_contact_people,
                supplier_phone:this.editForm.supplier_phone
            };
            resName
                .editSuppliers(params)
                .then(res => {
                //
                if (res.data.code === 1) {
                    this.$message({
                    message: "",
                    type: "success"
                    });
                    this.editFormVisible = false;
                    this.supplierdata();
                } else if (res.data.code === -2) {
                    this.$message({
                    message: res.msg,
                    type: "error"
                    });
                }
                })
                .catch(err => {
                //
                return false;
                });
           }
        })
    },
    // 
    handleDel(index, row) {
      this.$confirm("?", "", {
        type: "warning"
      }).then(() => {
        let params = {
          id: row.id
        };
        resName
          .delSuppliers(params)
          .then(res => {
            //
            if (res.data.code === 1) {
              this.$message({
                message: "",
                type: "success"
              });
              this.supplierdata();
            } else if (res.code === -2) {
              this.$message({
                message: res.msg,
                type: "error"
              });
            }
          })
          .catch(err => {
            //
            return false;
          });
      });
    }
  }
};
</script>
<style lang="less" scoped>
.toolbar,
.el-table {
  background: #fff;
}
.toolbar {
  margin-bottom: 15px;
}
.el-form-item.left {
  float: left;
}
</style>



